@import url(https://fonts.googleapis.com/css?family=Open+Sans|Roboto:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Sacramento&display=swap);
@font-face {
   font-family: Clip;
   src: url("https://acupoftee.github.io/fonts/Clip.ttf"); 
}  

:root {
  --lightBlue: #1c2b3f;
  --darkBlue: #182135;
  --mainOrange: #f7902b;
  --mainYellow: #ffa400;
  --mainGreen: #008000;
  --lightGreen:#00fff2;
  --goldenYellow: #fff6a9;
  --clr-grey: #8a8c8c;
  --clr-grey-4: hsl(209, 28%, 39%);
  --clr-white: #f0f0f0;
  --softWhite: #FBFAF5;
  --softGrey: #acaea9;
  --heroWhite: #ffe6ff;
  --clr-black: #000;
  --lightRed: #ff65bd;
  --ff-primary: "Roboto", sans-serif;
  --ff-secondary: "Open Sans", sans-serif;
  --spacing: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --mainGold: #daa520;
}

html, *, body {
  scroll-behavior: smooth !important;
  -webkit-overflow-scrolling: touch !important;
}
*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: #1c2b3f;
  background: var(--lightBlue); 
  color: var(--clr-grey-1);
  overflow-x: hidden;
  font-size: 0.875rem;
  box-sizing: border-box; 
}
/* -------------------------------------------------------------popup modal */
.modal-footer{
  display: flex !important;
  align-items: center !important;
  justify-content: center;
}
#exampleModalCenter{
  background-color: rgba(0,0,0, 0.7) !important;
 
}
/* -------------------------------------------------------------------end popup modal */
a {
  text-decoration: none;
}


a[href^="tel:"]:before{
  content: "\260e";
  margin-right: 10px;
}

main {
  width: 70%;
  padding-left: 1rem;
}

main h2 {
  color: Var(--heroWhite) !important;
  font-size: 1.2rem !important;
  text-transform: uppercase;
  text-decoration: underline;
}

main h4 {
  color: Var(--softWhite) !important;
  font-size: 1.2rem !important;
}

main p {
  font-size: 1.1rem;
  color: Var(--softWhite) !important;
}

main ul li {
  color: Var(--softWhite) !important;
  font-size: 1.1rem;
  list-style-type: none;
}
.callnow-container, .treasurer-links{
display: flex;
align-items: center;
justify-content: space-around;
}
.top-nav {
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #182135;
  background-color: var(--darkBlue);
  /* height: 102px; */
  margin: 0 auto;
  padding-right: 2rem;
  padding-left: 2rem;
  
}
.support{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Clip"; 
   color: #FBFAF5; 
   color: var(--softWhite); 
   text-transform: capitalize;
 
}
.text1, .text2, .call-link, .whatsapp-link {

  font-weight: bold;
  font-size: 2rem;
  text-align: center;
  text-transform: uppercase;
  font-weight: bolder;
  font-family: "Clip";
  color: #ffe6ff;
  color: var(--heroWhite);
}
.text2{
  font-size: 1.5rem;
  font-weight: bold;
}
.title3{
  margin: 0 auto;
}
.title4{
  margin-top: 3rem;
 }

/* ----------------------------------------------------------------navbar */
nav{
  width: 100%;
  max-width: 100vw;
  z-index: 2;
  background-color: #f0f0f0;
  background-color: var(--clr-white);
}
.nav-center{
  height: 100%;
  width: inherit;  
}

.nav-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  padding-right: 1rem;


}
.nav-toggle {
  font-size: 1.5rem;
  color: #008000;
  color: var(--mainGreen);
  background: transparent;
  transition: var(--transition);
  cursor: pointer;
}
.nav-toggle:hover {
  color: var(--clr-secondary);
}

.links-container {
  transition: var(--transition);
  background: #1c2b3f;
  background: var(--lightBlue);
  width: inherit;
  
}
.links-container .links {
  padding-left: 1rem
}

.links a {
   font-size: 1rem; 
  text-transform: uppercase;
  letter-spacing: 0;
  display: block;
  transition: var(--transition);
  font-weight: bold;
  color: #ffe6ff;
  color: var(--heroWhite);
  padding: 0.75rem 0;
  text-decoration: none;
}
.dropdown {
  display: inline-block;
  overflow: visible !important;
  
}
.dropdown-content {
  position: absolute;
  display: none;
  background-color: #182135;
  background-color: var(--darkBlue);
  margin-top: 2.33rem;
  width: auto;
  min-width: 200px;
  z-index: 999 !important;
}
.dropdown-content .dropdown-link {
  color: #f0f0f0;
  color: var(--clr-white);
  text-decoration: none;
  display:flex ;
  flex-direction: column;
  align-items: flex-start;
  min-width: 100%;
  padding-left: 3px;
  border-bottom: 0.1px solid #000;
  border-bottom: 0.1px solid var(--clr-black);
}
.dropdown-content .dropdown-link:hover {
  color: #000;
  color: var(--clr-black);
   
}
.dropdown:hover .dropdown-content {
  display: block;
}
.dropdown:hover .dropdown-link {
  background-color: #8a8c8c;
  background-color: var(--clr-grey);
}

.register-link{
  display: flex;
  justify-content: space-between;
  align-items: center;

}

.info-wrapper{
  background-color: #1c2b3f;
  background-color: var(--lightBlue);
  white-space: nowrap;
   overflow-x: hidden;
   height: 30px;
   flex-direction: column;
   padding-bottom: 0;
   display: grid;
   place-items: center;
   left: 0;
   right: 0;
   top: 62.48px;
   z-index: -1;
  
}

.info{
  -webkit-transform: translate(250%);
          transform: translate(250%);
  -webkit-animation: info-bar 20s linear infinite;
          animation: info-bar 20s linear infinite;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  text-transform: capitalize;
  color: red;
  font-size: 0.8rem;
  margin: 0;
 }

 /* ----------------------------------------------------------Key frames */
@-webkit-keyframes info-bar {
  0% {
    -webkit-transform: translate(250%);
            transform: translate(250%);
     
  }
 100% {
    -webkit-transform: translate(-300%);
            transform: translate(-300%);
  }
}
@keyframes info-bar {
  0% {
    -webkit-transform: translate(250%);
            transform: translate(250%);
     
  }
 100% {
    -webkit-transform: translate(-300%);
            transform: translate(-300%);
  }
}
@-webkit-keyframes info-bar-mobile {
  0% {
    -webkit-transform: translate(200%);
            transform: translate(200%);
  }
  100% {
    -webkit-transform: translate(-250%);
            transform: translate(-250%);
  }
}
@keyframes info-bar-mobile {
  0% {
    -webkit-transform: translate(200%);
            transform: translate(200%);
  }
  100% {
    -webkit-transform: translate(-250%);
            transform: translate(-250%);
  }
}

/* ----------------------------------------------------------------tables */
 .list-of-all-contributions{
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
} 

/* ---------------------------------------------------------------------hero */
 
.banner {
     background-repeat: no-repeat;
      background-size: cover; 
      background-position: center; 
      width: 100vw;
}
 
.container-in-banner{
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 100%;
   
}
  .container-in-banner .value {
    color: #ffe6ff;
    font-size: 1rem;
  }
  .container-in-banner h6 {
   font-size: 1rem;
  }
.container-in-banner p {
  color: #f0f0f0;
  color: var(--clr-white);
  max-width: 25rem;
  margin: 0 auto;
  margin-bottom: 1.25rem;
  -webkit-animation: run-left 2s ease;
          animation: run-left 2s ease;
}
.container-in-banner .count-up-wrapper{
  height: 100px;
  width: 100px ;
  padding: 10px ;
  border-radius: 35px;
  display: grid;
  place-items: center;
  background-color: transparent;
  border: 2px solid #f7902b;
  border: 2px solid var(--mainOrange);
 }

/* ---------------------------------------------------------------------branch hero */
.page-hero {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  min-height: 30vh;
  left: 0;
  right: 0;
  width: 100%;
  background: #182135;
  background: var(--darkBlue);
  margin-bottom: 4rem;
}

.page-hero-text {
  text-align: center;
  color: #ffe6ff;
  color: var(--heroWhite);
  text-transform: uppercase;
  font-family: "Clip";
  font-weight: bolder;
}

/* -------------------------------------------------------------------All Projects */
.underline {
  margin: 0 auto;
  height: 3px;
  width: 50px;
  background-color: #daa520;
  background-color: var(--mainGold);
}
.posts-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 auto;
  width: 100%;
 
}
.post-container {
  background: #e2e8d5;
  display: flex;
  flex-direction: column;
  padding: 3%;
  margin: 1% 2%;
  width: 450px;
}
.post-footer{
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.post-tiltle-wrapper{
  height: 100px;
}
.image {
  width: 100%;
  height: 210px;
}

/* ---------------------------------------------------------------------chief */
.chief-container {
  background: #182135;
  background: var(--darkBlue);
  display: flex;
  flex-direction: column;
  
}
 
.chief-wrapper{
   width: 100%;
   margin: 0 auto;
    
}
.chief-wrapper img{
   max-width: 100%;
   height: 100%;
   width: 100%;
}

/* ---------------------------------------------------------------------becuda executive */
 .great-icons{
  width: 100%;
  margin: 0 auto;
  margin-bottom: 5rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
   grid-column-gap: 1rem;
   grid-row-gap: 1rem;  
  height: auto;
   
} 
.becuda-member{
  background: #182135;
  background: var(--darkBlue);
  width: 350px;
  max-width: 350px;
  margin: 0 auto;
}
.becuda-image-wrapper{
  width: 100% !important;   
  margin: 0 auto;
  
}
.becuda-image-wrapper img{
  width: 100%;
 max-width: 100%; 
}

/* ----------------------------------------------------------befand youths association */
.motto {
  color:#ffe6ff;
  color:var(--heroWhite);
  font-family: "Sacramento", cursive;
  text-transform: capitalize;
  font-size: 2.5rem;
  font-weight: bolder;
  
}
.beyoin-counter{
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 20%;
}
.beyoin-counter .value {
  color: #ffe6ff;
  
}
/* Latest news */
.latest-news{
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.home-btn {
  width: 100%;
  height: 50px;
  text-align: center;
  text-transform: capitalize;
  font-weight: bold;
  margin-bottom: 40px;
}
/* ------------------------------------------------------------Buttons */
.btns{
  text-transform: capitalize;
  font-size: 1.4rem;
  background: #f7902b;
  background: var(--mainOrange);
  border: 0.05rem solid #f7902b;
  border: 0.05rem solid var(--mainOrange);
  color: #f0f0f0;
  color: var(--clr-white);
  border-color: #f7902b;
  border-color: var(--mainOrange);
  border-radius: 0.5rem;
  padding: 0.2rem 0.5rem;
  cursor: pointer;
  margin: 0.2rem 0.5rem 0.2rem 0;
  transition: All 0.5s ease-in-out;
  
}

.btns:hover {
  background-color:  #f0f0f0;
  background-color:  var(--clr-white);
  color: #f7902b;
  color: var(--mainOrange);
}
.btns:focus {
  outline: none;
}

/* End Buttons */
/* contribute page */
.contribute-page{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.contribute-page .contribute-page-child{
  display: grid;
  place-items: center;
  border-radius: 5px;
  width: 50%;
  height: 50px;
  margin: 2px;
  text-decoration: none;
}
.treasurer-details{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
}
/* end contribute page */
/*Footer*/
.footer-bg {
  background-color: #000 !important;
  background-color: var(--clr-black) !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  width: 100%;
  max-width: 100vw;
  
}

/* end of footer */
 
